@import url('https://fonts.googleapis.com/css?family=Barlow');.container{width:100%;position:relative;overflow:hidden}
a{text-decoration:none}h1.main,p.demos{-webkit-animation-delay:18s;animation-delay:18s}
.sp-container{position:fixed;top:196px;left:0;width:100%;height:100%;z-index:0;background:-webkit-radial-gradient(rgba(141,138,138,0.966),rgba(185,181,181,0.911) 35%,rgba(236,232,232,0.7));background:-o-radial-gradient(rgba(141,138,138,0.966),rgba(185,181,181,0.911) 35%,rgba(236,232,232,0.7));background:radial-gradient(rgba(141,138,138,0.966),rgba(185,181,181,0.911) 35%,rgba(236,232,232,0.7))}
.sp-content{position:absolute;width:100%;height:100%;left:0;margin-top:-80px;z-index:1000;font-size:20px}
.sp-container h2{position:absolute;top:50%;line-height:100px;height:90px;margin-top:-50px;font-size:90px;width:100%;text-align:center;color:transparent;-webkit-animation:blurFadeInOut 3s ease-in backwards;animation:blurFadeInOut 3s ease-in backwards}
.sp-container h2.frame-1{-webkit-animation-delay:0s;animation-delay:0s}
.sp-container h2.frame-2{-webkit-animation-delay:3s;animation-delay:3s}
.sp-container h2.frame-3{-webkit-animation-delay:6s;animation-delay:6s}
.sp-container h2.frame-4{-webkit-animation-delay:9s;animation-delay:9s}
.sp-container h2.frame-5{-webkit-animation:none;animation:none;text-shadow:#f9c}
.sp-container h2.frame-5 span{-webkit-animation:blurFadeIn 3s ease-in 12s backwards;animation:blurFadeIn 3s ease-in 12s backwards;color:#f9c}
.sp-container h2.frame-5 span:nth-child(2){-webkit-animation-delay:13s;animation-delay:13s}
.sp-container h2.frame-5 span:nth-child(3){-webkit-animation-delay:14s;animation-delay:14s}
.sp-globe{position:absolute;width:282px;left:50%;top:50%;margin:-137px 0 0 -141px;-webkit-animation:fadeInBack 3.6s linear 14s backwards;animation:fadeInBack 3.6s linear 14s backwards;-ms-filter:"alpha(opacity=30)";filter:alpha(opacity=30);opacity:.3;-webkit-transform:scale(5);-ms-transform:scale(5);transform:scale(5)}
@-webkit-keyframes blurFadeInOut{0%{opacity:0;text-shadow:0 0 40px #fff;-webkit-transform:scale(1.3)}
20%,75%{opacity:1;text-shadow:0 0 1px #fff;-webkit-transform:scale(1)}
100%{opacity:0;text-shadow:0 0 50px #fff;-webkit-transform:scale(0)}
}@-webkit-keyframes blurFadeIn{0%{opacity:0;text-shadow:0 0 40px #fff;-webkit-transform:scale(1.3)}
50%{opacity:.5;text-shadow:0 0 10px #fff;-webkit-transform:scale(1.1)}
100%{opacity:1;text-shadow:0 0 1px #fff;-webkit-transform:scale(1)}
}@-webkit-keyframes fadeInBack{0%{opacity:0;-webkit-transform:scale(0)}
50%{opacity:.4;-webkit-transform:scale(2)}
100%{opacity:.2;-webkit-transform:scale(5)}
}@-webkit-keyframes fadeInRotate{0%{opacity:0;-webkit-transform:scale(0) rotate(360deg)}
100%{opacity:1;-webkit-transform:scale(1) rotate(0deg)}
}@keyframes blurFadeInOut{0%{opacity:0;text-shadow:0 0 40px #fff;-webkit-transform:scale(1.3);transform:scale(1.3)}
20%,75%{opacity:1;text-shadow:0 0 1px #fff;-webkit-transform:scale(1);transform:scale(1)}
100%{opacity:0;text-shadow:0 0 50px #fff;-webkit-transform:scale(0);transform:scale(0)}
}@keyframes blurFadeIn{0%{opacity:0;text-shadow:0 0 40px #fff;-webkit-transform:scale(1.3);transform:scale(1.3)}
50%{opacity:.5;text-shadow:0 0 10px #fff;-webkit-transform:scale(1.1);transform:scale(1.1)}
100%{opacity:1;text-shadow:0 0 1px #fff;-webkit-transform:scale(1);transform:scale(1)}
}@keyframes fadeInBack{0%{opacity:0;-webkit-transform:scale(0);transform:scale(0)}
50%{opacity:.4;-webkit-transform:scale(2);transform:scale(2)}
100%{opacity:.2;-webkit-transform:scale(5);transform:scale(5)}
}@keyframes fadeInRotate{0%{opacity:0;-webkit-transform:scale(0) rotate(360deg);transform:scale(0) rotate(360deg)}
100%{opacity:1;-webkit-transform:scale(1) rotate(0deg);transform:scale(1) rotate(0deg)}
}@media screen and (max-width:601px){.frame-1,.frame-2,.frame-3,.frame-4,.frame-5{font-size:10px}
}


