.fileInput{cursor:pointer;border:0}
.previewText{width:100%;margin-top:20px}
html{font-size:16px}body{font:100% / 1.414 sans-serif}
.uploadForm{margin-right:auto;margin-left:auto;width:90%}
fieldset{padding:0;margin:0;border:0}
label{margin-bottom:8px;display:block}
input:not([type=checkbox]):not([type=radio]),textarea{padding:8px;width:100%;border-top:0;border-right:0;border-left:0}
input[type=checkbox],input[type=radio]{margin-right:8px}
textarea{resize:vertical}.submitButton{margin-top:5px;margin-right:auto;margin-left:auto;display:block;padding:8px 16px;font-size:16px;color:#fff;background-color:#3fc1c9;border:0;border-radius:2px;cursor:pointer;-webkit-transition:background-color .15s ease-in;-o-transition:background-color .15s ease-in;transition:background-color .15s ease-in}
.button:focus:active{background-color:#31a7ae}
.button:focus{outline:0}.button:hover{background-color:#7bd4da}
.description{margin-right:2%;height:100px;white-space:pre-wrap;white-space:-moz-pre-wrap;white-space:-pre-wrap;white-space:-o-pre-wrap;word-wrap:break-word}
.flexseal{background-color:ghostwhite}
body{font-family:Arial,sans-serif;background-size:cover;height:100vh}
.button{font-size:1em;padding:10px;border-radius:20px/50px;text-decoration:none;cursor:pointer;-webkit-transition:all .3s ease-out;-o-transition:all .3s ease-out;transition:all .3s ease-out}
.button:hover{background:#06d85f}
.submitButton{display:center;margin-top:0}
.submitButton:hover,.custom-file-upload:hover{background-color:cyan}
.flextape{display:-ms-flexbox;display:flex;width:100%;margin-left:0;-ms-flex-pack:center;justify-content:center;display:-webkit-flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:space-evenly;justify-content:space-evenly;text-align:left}
#textInput{width:98%}.formHeader,.uploadContactForm,.formHeaderMobile{width:35%;margin-bottom:auto;background-color:ghostwhite;margin-top:30px}
.formHeaderMobile{display:none}
@media only screen and (max-width:1000px){.uploadContactForm,.formHeader{width:45%}
}@media only screen and (max-width:900px){.uploadContactForm,.formHeader{width:90%}
.formHeader{display:none}.formHeaderMobile{display:block}
}@media only screen and (max-width:800px){.uploadContactForm,.formHeader,.formHeaderMobile{width:90%}
.formHeaderMobile{display:block}
}.formHeader,.formHeaderMobile{text-align:center;background-color:#f9c;font-size:14px;text-transform:uppercase;color:#fdfdfd;border-radius:5px}


