*{text-decoration:none;list-style:none;margin:0;padding:0;outline:0}
input:focus,select:focus{background:ghostwhite}
body{margin:0;padding:0;font-family:'Open Sans',sans-serif}
section{width:100%;max-width:1200px;margin:0 auto;display:table;position:relative}
h1{margin:0 auto;display:table;font-size:26px;padding:40px 0;color:#c2c1c1ee;text-align:center}
h1 span{font-weight:500}
header{width:100%;display:table;background-color:#f9c;margin-bottom:50px}
#logo{float:left;font-size:24px;text-transform:uppercase;color:#fff;font-weight:600;padding:20px 0}
nav{width:auto;float:right}
nav ul{display:table;float:right}
nav ul li{float:left}
nav ul li:last-child{padding-right:0}
nav ul li div{color:#c2c1c1ee;font-size:18px;padding:25px 20px;display:inline-block;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
nav ul li div:hover{background-color:#c2c1c1ee;color:#fff;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
nav ul li div:hover i{color:#f9c;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
nav ul li div i{padding-right:10px;color:#002e5b;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
.toggle-menu ul{display:table;width:25px}
.toggle-menu ul li{width:100%;height:3px;background-color:#fff;margin-bottom:4px}
.toggle-menu ul li:last-child{margin-bottom:0}
section{max-width:95%}
.notHidden{display:inline}
header{padding:20px 0}
#logo{padding:0}
.toggle-menu{float:right;padding:8px 0;display:block;cursor:pointer}
nav{position:absolute;right:-20px;top:53px;background-color:#c0c0c0;padding:0;z-index:99}
nav ul{width:auto}
nav ul li{float:none;padding:0;width:100%;display:table}
nav ul li a{color:#fff;font-size:15px;padding:10px 20px;display:block;border-bottom:1px solid rgba(225,225,225,0.1)}
nav ul li a i{color:#c2c1c1ee;padding-right:13px}
nav{margin-top:8px;display:block}
nav ul li div{color:#c2c1c1ee;font-size:18px;padding:15px 10px;display:inline-block;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s;width:220px}
nav ul li div i{color:#fff;font-size:18px;display:inline-block;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
nav ul li div:hover{background-color:#f9c;color:#c2c1c1ee;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
nav ul li div:hover i{background-color:#f9c;color:#c2c1c1ee;-webkit-transition:all .5s ease 0s;-o-transition:all .5s ease 0s;transition:all .5s ease 0s}
@media only screen and (max-width:980px){.content{width:90%}
}
@media only screen and (max-width:480px){section{max-width:90%}
}
@media only screen and (max-width:360px){h1{font-size:20px;margin-bottom:8px}
}
.hidden{display:none;-webkit-transition:opacity 400ms;-o-transition:opacity 400ms;transition:opacity 400ms}


